<template>
  <div></div>
</template>

<script>
export default {
  methods: {
    tip () {
      this.$swal.fire({
        title: '?',
        text: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: '!',
        cancelButtonText: ''
      }).then(result => {
        if (result.isConfirmed) {
          this.$swal.fire(
            '!',
            '!',
            'success'
          );
          this.deleteAll();
        }
      });
    },
    deleteAll () {
      this.$emit('delete-all');
    }
  }
};
</script>
