<template>
  <div></div>
</template>

<script>
export default {
  props: ['item'],
  data () {
    return {
      product: this.item
    };
  },
  watch: {
    item: function () {
      if (this.item.id !== this.product.id) {
        this.product = this.item;
      }
    }
  },
  methods: {
    tip () {
      this.$swal.fire({
        title: '?',
        text: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: '!',
        cancelButtonText: ''
      }).then(result => {
        if (result.isConfirmed) {
          this.$swal.fire(
            '!',
            '',
            'success'
          );
          this.getCart();
        }
      });
    },
    getCart () {
      this.$emit('delete-cart', this.item);
    }
  },
  mounted () {
    this.product = this.item;
  }
};
</script>
