<template>
  <div class="container-fluid">
    <div class="row bg-primary" style="height:10vh;">
    </div>
    <div class="row pt-5 mb-3 pb-1 justify-content-between bg-primary" style="border-bottom:solid 1px white;">
      <div class="col-12 col-md-12 col-lg-4 col-sm-12">
        <h2 style="color:white"></h2>
      </div>
    <div class="col-12 col-md-12 col-lg-4 col-sm-12">
      <div class="buttonG">
        <img src="../assets/img/arrow_back_ios_white_24dp.svg" alt="arrow">
        <button class="goProductbtn btn btn-lg px-3 py-2" type="button" id="continue" @click="goProductList()"></button>
      </div>
    </div>
    </div>
    <div class="row justify-content-around mb-3">
      <div class=" col-lg-4 col-md-6 col-sm-12 d-flex px-3  ">
        <div class="stepHint border border-gray py-3 px-3 mx-2 text-white" style=" width:10rem; height:3.5rem; background-color:#627364;">
          <p class="fw-bold"></p>
        </div>
        <div class="stepHint border border-gray py-3 px-3 mx-2 text-white opacity-50" style=" width:10rem; height:3.5rem; background-color:#627364;">
          <p class="fw-bold"></p>
        </div>
        <div class="stepHint border border-gray py-3 px-3 mx-2 text-white opacity-50" style=" width:10rem; height:3.5rem; background-color:#627364;">
          <p class="fw-bold"></p>
        </div>
      </div>
    </div>
    <div class="row"> <!--cartTable-->
      <CartTable :get-cart="cartData" @delete-cart="deleteCart" @get-cart-order="getCartList" />
    </div>
    <div class="row justify-content-center pt-5 px-5">
      <div class="col-12 col-sm-12 col-lg-5 col-md-10">
        <div class="input-group input-group-lg">
          <input type="text" class="form-control text-info ps-2" v-model="couponInput" placeholder=" MyGuiTar555" aria-label="coupon" aria-describedby="button-addon2">
          <button class="actionBtn btn btn-outline" type="button button-outline px-3 py-2" id="button-addon2" @click="useCoupon(couponInput)"></button>
        </div>
        <label class="form-check-label" id="couponCheck"  v-if="couponBoolean">
          <p class="text-danger">:MyGuiTar555</p>
          <p class="text-danger">p.s. </p>
        </label>
        <label class="form-check-label" id="couponCheck" v-if="rightCoupon">
          <p class="text-success" >{{ couponCheck.message }}</p>
        </label>
        <label class="form-check-label" id="couponCheck"  v-if="errorCouponMessage">
          <p class="text-danger"> MyGuiTar555 </p>
        </label>
      </div>
    </div>
    <div class="row justify-content-end ">
      <div class="col-12 col-sm-12 col-md-12 col-lg-4">
        <div class="next justify-content-start">
          <button type="button" class="actionBtn btn btn-lg px-3 py-2 mx-2" :disabled="clearAllCartBoolean === false" @click="openDeleteAllTip"></button>
          <button type="button" class="btn btn-lg btn-primary px-3 py-2 mx-2" @click="goForm()"></button>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-12 col-sm-12 col-lg-12 col-md-12">
        <div class="notification p-5 bg-warning">
          <div class="buy">
            <h3></h3>
            <p>* </p>
            <p>* 5()</p>
            <p>* </p>
            <p>* </p>
          </div>
          <div class="back">
            <h3>()/()</h3>
            <p>* 77</p>
            <p>*  () </p>
            <p>* </p>
          </div>
        </div>
      </div>
    </div>
  </div>
  <DeleteAllCartMessage ref="deleteall" @delete-all="deleteAllCart" />
  <ApiLoading ref="load" />
</template>

<script>
import CartTable from '@/components/CartTable.vue';
import ApiLoading from '@/components/ApiLoading.vue';
import DeleteAllCartMessage from '@/components/DeleteAllCartMessage.vue';
import NoCartCantGoFromAlert from '@/utils/noCartCantGoFromAlert.js';
import emitter from '@/utils/emitter.js';
export default {
  mixins: [NoCartCantGoFromAlert],
  components: {
    CartTable,
    ApiLoading,
    DeleteAllCartMessage
  },
  data () {
    return {
      cartData: [],
      couponInput: '',
      couponCheck: '',
      clearAllCartBoolean: false,
      couponBoolean: true, // 
      errorCouponMessage: false, // 
      rightCoupon: false
    };
  },
  methods: {
    goProductList () {
      this.$router.push('/productList');
    },
    goForm () {
      if (this.cartData.carts.length === 0) {
        this.openNoCartTip();
      } else {
        this.$router.push('/form');
      }
    },
    getCartList () {
      this.$refs.load.doAjax();
      this.$http.get(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`)
        .then(res => {
          this.cartData = res.data.data;
          if (this.cartData.carts.length !== 0) {
            this.clearAllCartBoolean = true;
          } else {
            this.clearAllCartBoolean = false;
          }
          this.$refs.load.timeIsOut();
        })
        .catch(err => {
          console.log(err);
          this.$refs.load.timeIsOut();
        });
    },
    deleteCart (item) {
      this.$http.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${item.id}`)
        .then(res => {
          this.getCartList();
          emitter.emit('get-cart');
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteAllCart () {
      this.$http.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/carts`)
        .then(res => {
          this.getCartList();
          emitter.emit('get-cart');
        })
        .catch(err => {
          console.log(err);
        });
    },
    useCoupon (couponInput) {
      const data = {
        code: couponInput
      };
      this.$http.post(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/coupon`, { data: data })
        .then(res => {
          this.$refs.load.doAjax();
          this.couponCheck = res.data;
          this.couponBoolean = false;
          this.rightCoupon = true;
          this.getCartList();
          emitter.emit('get-cart');
        })
        .catch(err => {
          console.log(err);
          this.errorCouponMessage = true;
          this.couponBoolean = false;
          this.rightCoupon = false;
        });
    },
    openDeleteAllTip () {
      this.$refs.deleteall.tip();
    },
    openNoCartTip () {
      this.noCartTip();
    }
  },
  mounted () {
    this.getCartList();
    emitter.on('delete-cart', () => {
      this.getCartList();
    });
  },
  unmounted () {
    emitter.off('delete-cart');
  }
};
</script>

<style lang="scss" scoped>
*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
.goProductbtn {
  text-align: center;
  font-size:1.5rem;
  color:#627364;
  transition:all 1s 0s ;
}
.goProductbtn:hover{
  background-color: #51423C;
  color:white;
}
.actionBtn{
  text-align: center;
  font-size:20px;
  color:#627364;
  border: 1px solid #627364;
}
.actionBtn:hover{
  background-color: #627364;
  color:white;
}
.next{
  display:flex;
  justify-content: center;
  padding:1.5rem;
}
.notification{
  text-align: left;
    .buy{
      margin-bottom: 1rem;
    }
    h3{
      margin-bottom: 1rem;
    }
}
#continue{
  color:white;
}
#continue:hover{
  background-color: #627364;
}
</style>
