export default {
  methods: {
    noCartTip () {
      this.$swal.fire({
        title: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: '!'
      }).then(result => {
        if (result.isConfirmed) {
          this.goProductList();
        }
      });
    },
    goProductList () {
      this.$router.push('/productList');
    }
  }
};
